<template lang="pug">
    v-card 
        v-card-text
            v-card-title
                span 玩客家交易API
            v-container
                v-row 
                    v-col(cols="12" sm="12")
                        v-text-field(outlined hide-details v-model="accessKey" label="AccessKey")
                    v-col(cols="12" sm="12")
                        v-text-field(outlined hide-details v-model="secretKey" label="SecretKey")
                    
            v-card-actions
                v-btn(color="success" dark @click="save") 保存
</template>

<script>
import * as api from "@/apis/wswebapi"
export default {
    data: () => ({
    accessKey: "",
    secretKey: "",
    ExName:"WKJ"
    }),
    methods:{
        save(){
            let params = {
                            "Accesskey":this.accessKey,
                            "Secretkey":this.secretKey,
                            "ExName":this.ExName};
            console.log(params)
            api.AddExchangeApi(params).then(res=>{
        if (res.code==200){
            console.log("更新API成功",res);
            this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"});
        }else{
            this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
        }
    })
        }
    }
}
</script>